<template>
    <div class="ship">
        <div class="shipOption">
            <div class="singleship">
                <el-button type="primary" @click="go_long_address"
                    >地址超长</el-button
                >
            </div>
            <div class="singleship">
                <el-button type="primary" @click="false_order"
                    >假回传单号查看</el-button
                >
            </div>
            <div class="firstUpload">
                <label
                    >易仓批量发货
                    <input
                        type="file"
                        value="文件上传"
                        ref="input1"
                        @change="changeFile1"
                        accept=".xls,.xlsx"
                /></label>
                <el-button
                    style="margin-left: 10px"
                    size="small"
                    type="primary"
                    @click="submitUpload1"
                    >提交</el-button
                >
            </div>
            <div class="secondUpload">
                <label
                    >易仓TXT发货
                    <input
                        type="file"
                        value="文件上传"
                        ref="input2"
                        @change="changeFile2"
                        accept=".txt"
                /></label>
                <el-button
                    style="margin-left: 10px"
                    size="small"
                    type="primary"
                    @click="submitUpload2"
                    >提交</el-button
                >
            </div>
            <div class="singleship">
                <el-button type="primary" @click="singleShip"
                    >单个订单发货</el-button
                >
            </div>
        </div>
        <div class="table_con">
            <el-table
                max-height="750"
                :data="tableData"
                style="width: 100%"
                border
                v-loading="loading"
                element-loading-text="拼命加载中,请稍等"
            >
                <el-table-column prop="order_id" label="订单号" width="250">
                </el-table-column>
                <el-table-column prop="country_code" label="国家" width="120">
                </el-table-column>
                <el-table-column prop="station" label="站点" width="120">
                </el-table-column>
                <el-table-column prop="reason" label="失败原因">
                </el-table-column>
                <el-table-column label="操作" width="300">
                    <template slot-scope="scope">
                        <el-button
                            type="primary"
                            @click="reShip(scope.row.order_id)"
                            >重新发货</el-button
                        >
                        <el-button
                            type="danger"
                            @click="delShip(scope.row.order_id)"
                            >删除</el-button
                        >
                        <el-button
                            type="warning"
                            @click="fix_data(scope.row.order_id)"
                            >修复</el-button
                        >
                    </template>
                </el-table-column>
            </el-table>
        </div>
        <!-- 重新发货 -->
        <div class="dialog">
            <el-dialog
                title="重新发货"
                :visible.sync="dialogVisible1"
                width="30%"
            >
                <el-form
                    :model="ship_one_form1"
                    :rules="rules1"
                    ref="ruleForm1"
                    label-width="200px"
                >
                    <el-form-item
                        prop="order_id"
                        label="请输入订单编号"
                        required
                    >
                        <el-input
                            v-model="ship_one_form1.order_id"
                            placeholder="请输入订单编号"
                        ></el-input>
                    </el-form-item>
                    <el-form-item label="请选择收件人国家" prop="country_code">
                        <el-input
                            v-model="ship_one_form1.country_code"
                        ></el-input>
                    </el-form-item>

                    <el-form-item label="请填写城市" prop="city">
                        <el-input
                            v-model="ship_one_form1.city"
                            placeholder="请填写城市"
                        ></el-input>
                    </el-form-item>

                    <el-form-item
                        prop="address"
                        label="请输入详细地址"
                        required
                    >
                        <el-input
                            type="textarea"
                            :autosize="{ minRows: 4, maxRows: 4 }"
                            v-model="ship_one_form1.address"
                            placeholder="请输入详细地址"
                        ></el-input>
                    </el-form-item>
                    <el-form-item prop="name" label="请输入收货人姓名" required>
                        <el-input
                            v-model="ship_one_form1.name"
                            placeholder="请输入收货人姓名"
                        ></el-input>
                    </el-form-item>

                    <el-form-item label="请输入电话" required prop="tel">
                        <el-input
                            v-model="ship_one_form1.tel"
                            placeholder="请输入电话"
                        ></el-input>
                    </el-form-item>
                    <el-form-item label="请输入邮编" required prop="zip_code">
                        <el-input
                            v-model="ship_one_form1.zip_code"
                            placeholder="请输入邮编"
                        ></el-input>
                    </el-form-item>

                    <div style="width: 50%"></div>

                    <el-form-item>
                        <el-button
                            class="submitbtn"
                            type="primary"
                            @click="onSubmit1('ruleForm1')"
                            >提交</el-button
                        >
                    </el-form-item>
                </el-form>
            </el-dialog>
        </div>

        <!-- 单个发货 -->
        <div class="dialog">
            <el-dialog
                title="单个发货"
                :visible.sync="dialogVisible"
                width="30%"
            >
                <el-form
                    :model="ship_one_form"
                    :rules="rules"
                    ref="ruleForm"
                    label-width="200px"
                >
                    <el-form-item prop="number" label="请输入订单编号" required>
                        <el-input
                            v-model="ship_one_form.number"
                            placeholder="请输入订单编号"
                        ></el-input>
                    </el-form-item>
                    <el-form-item label="请选择收件人国家">
                        <el-input
                            v-model="ship_one_form.receive_country"
                        ></el-input>
                    </el-form-item>

                    <!-- <el-form-item label="请填写州/省">
            <el-input
              v-model="ship_one_form1.province"
              placeholder="请填写州/省"
            ></el-input>
          </el-form-item> -->
                    <el-form-item label="请填写城市">
                        <el-input
                            v-model="ship_one_form.city"
                            placeholder="请填写城市"
                        ></el-input>
                    </el-form-item>

                    <el-form-item
                        prop="address"
                        label="请输入详细地址"
                        required
                    >
                        <el-input
                            type="textarea"
                            :autosize="{ minRows: 4, maxRows: 4 }"
                            v-model="ship_one_form.address"
                            placeholder="请输入详细地址"
                            style="resize: none"
                        ></el-input>
                    </el-form-item>
                    <el-form-item
                        prop="receive_name"
                        label="请输入收货人姓名"
                        required
                    >
                        <el-input
                            v-model="ship_one_form.receive_name"
                            placeholder="请输入收货人姓名"
                        ></el-input>
                    </el-form-item>

                    <el-form-item label="请输入电话" required prop="tel">
                        <el-input
                            v-model="ship_one_form.tel"
                            placeholder="请输入电话"
                        ></el-input>
                    </el-form-item>
                    <el-form-item label="请输入邮编" required prop="postcode">
                        <el-input
                            v-model="ship_one_form.postcode"
                            placeholder="请输入邮编"
                        ></el-input>
                    </el-form-item>

                    <!-- <el-form-item
            label="自定义编码"
            required
            prop="custom_code"
            style="margin-left: 4%"
          >
            <el-input
              v-model="ship_one_form1.custom_code"
              placeholder="自定义编码"
            ></el-input>
          </el-form-item> -->
                    <div style="width: 50%"></div>

                    <el-form-item>
                        <el-button
                            class="submitbtn"
                            type="primary"
                            @click="onSubmit('ruleForm')"
                            >提交</el-button
                        >
                    </el-form-item>
                </el-form>
            </el-dialog>
        </div>

        <!-- 修复弹框 -->
        <div class="dialog">
            <el-dialog title="修复" :visible.sync="dialogVisible2" width="30%">
                <el-form
                    :model="ship_one_form2"
                    :rules="rules2"
                    ref="ruleForm2"
                    label-width="200px"
                >
                    <el-form-item
                        prop="number"
                        label="请输入海外仓订单编号"
                        required
                    >
                        <el-input
                            v-model="ship_one_form2.number"
                            placeholder="请输入海外仓订单编号"
                        ></el-input>
                    </el-form-item>

                    <div style="width: 50%"></div>
                    <el-form-item>
                        <el-button
                            class="submitbtn"
                            type="primary"
                            @click="onSubmit2('ruleForm2')"
                            >提交</el-button
                        >
                    </el-form-item>
                </el-form>
            </el-dialog>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            submitFlag1: false,
            submitFlag2: false,
            tableData: [
                {
                    order_id: "",
                    country_code: "",
                    station: "",
                    reason: "",
                },
            ],
            dialogVisible: false,
            dialogVisible1: false,
            receive_countries: ["美国", "日本"],
            //单个订单发货数据
            ship_one_form: {
                order_id: "",
                country_code: "",
                // province: "",
                city: "",
                address: "",
                name: "",
                tel: "",
                zip_code: "",
                // custom_code: "",
            },
            rules: {
                number: [
                    {
                        required: true,
                        message: "请输入订单编号",
                        trigger: "blur",
                    },
                ],
                address: [
                    {
                        required: true,
                        message: "请输入详细地址",
                        trigger: "blur",
                    },
                ],
                receive_name: [
                    {
                        required: true,
                        message: "请输入收货人姓名",
                        trigger: "blur",
                    },
                ],
                tel: [
                    { required: true, message: "请输入电话", trigger: "blur" },
                ],
                postcode: [
                    { required: true, message: "请输入邮编", trigger: "blur" },
                ],
                // custom_code: [
                //   { required: true, message: "自定义编码", trigger: "blur" },
                // ],
            },
            //重新发货数据
            ship_one_form1: {
                order_id: "",
                country_code: "",
                // province: "",
                city: "",
                address: "",
                name: "",
                tel: "",
                zip_code: "",
                // custom_code: "",
            },
            rules1: {
                number: [
                    {
                        required: true,
                        message: "请输入订单编号",
                        trigger: "blur",
                    },
                ],
                address: [
                    {
                        required: true,
                        message: "请输入详细地址",
                        trigger: "blur",
                    },
                ],
                receive_name: [
                    {
                        required: true,
                        message: "请输入收货人姓名",
                        trigger: "blur",
                    },
                ],
                tel: [
                    { required: true, message: "请输入电话", trigger: "blur" },
                ],
                postcode: [
                    { required: true, message: "请输入邮编", trigger: "blur" },
                ],
                // custom_code: [
                //   { required: true, message: "自定义编码", trigger: "blur" },
                // ],
            },
            //修复弹框
            dialogVisible2: false,
            //修复数据
            ship_one_form2: {
                order_id: "",
                number: "",
            },
            rules2: {
                number: [
                    {
                        required: true,
                        message: "请输入海外仓订单编号",
                        trigger: "blur",
                    },
                ],
            },
            repair_order_id: "",
            formData1: null,
            formData2: null,
            loading: false,
        };
    },
    props: ["type"],
    mounted() {
        console.log("是不是这个页面");
        this.get_data();
    },
    methods: {
        // 单个订单发货
        singleShip() {
            this.dialogVisible = true;
        },
        handleClose1(done) {
            this.$confirm("确认关闭？")
                .then((_) => {
                    done();
                })
                .catch((_) => {});
        },
        handleClose(done) {
            this.$confirm("确认关闭？")
                .then((_) => {
                    done();
                })
                .catch((_) => {});
        },
        //单个订单发货提交
        onSubmit(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    console.log("验证通过，发起请求！");
                    this.dialogVisible = false;
                    let url = "server/order_management/single_order_deliver/";
                    let formData = new FormData();
                    formData.append("order_id", this.ship_one_form.order_id);
                    formData.append(
                        "country_code",
                        this.ship_one_form.country_code
                    );
                    formData.append("city", this.ship_one_form.city);
                    formData.append("address", this.ship_one_form.address);
                    formData.append("name", this.ship_one_form.name);
                    formData.append("tel", this.ship_one_form.tel);
                    formData.append("zip_code", this.ship_one_form.zip_code);
                    console.log(this.ship_one_form);
                    this.$axios.post(this._url2 + url, formData).then((res) => {
                        console.log("编辑数据提交", res.data);
                        if (res.data.code === 200) {
                            alert(res.data.mag);
                            window.location.reload();
                        } else {
                            alert(res.data.msg);
                        }
                    });
                } else {
                    console.log("error submit!!");
                    return false;
                }
            });
        },
        // 重新发货提交
        onSubmit1(formName) {
            window.location.reload();
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    console.log("重新发货！");
                    this.dialogVisible1 = false;
                    let url = "server/order_management/single_order_deliver/";
                    let formData = new FormData();
                    formData.append("order_id", this.ship_one_form1.order_id);
                    formData.append(
                        "country_code",
                        this.ship_one_form1.country_code
                    );
                    formData.append("city", this.ship_one_form1.city);
                    formData.append("address", this.ship_one_form1.address);
                    formData.append("name", this.ship_one_form1.name);
                    formData.append("tel", this.ship_one_form1.tel);
                    formData.append("zip_code", this.ship_one_form1.zip_code);
                    formData.append("err_order", "Y");
                    this.$axios.post(this._url2 + url, formData).then((res) => {
                        console.log("编辑数据提交", res.data);
                        if (res.data.code === 200) {
                            alert(res.data.msg);
                            window.location.reload();
                        } else {
                            alert(res.data.msg);
                        }
                    });
                } else {
                    console.log("error submit!!");
                    return false;
                }
            });
        },
        changeFile1() {
            if (this.type === "template") return;
            console.log(1);
            let files = this.$refs.input1.files[0];
            console.log(files.name);
            if (files.name) {
                let hz = files.name.split(".")[1];
                console.log(hz);
                if (hz !== "xls" && hz !== "xlsx") {
                    this.$refs.input1.files = null;
                    alert("请上传表格文件");
                    return;
                }
            }
            this.submitFlag1 = true;
            this.formData1 = new FormData();
            this.formData1.append("files", files);
        },
        changeFile2() {
            if (this.type === "template") return;
            console.log(2);
            let files = this.$refs.input2.files[0];
            if (files.name) {
                let hz = files.name.split(".")[1];
                if (hz !== "txt") {
                    this.$refs.input2.files = null;
                    alert("请上传txt文件");
                    return;
                }
            }
            this.changeFile2 = true;
            this.formData2 = new FormData();
            this.formData2.append("files", files);
        },
        //易仓批量发货
        submitUpload1() {
            if (this.type === "template" || !this.submitFlag1) return;
            console.log(3);
            let url = "server/order_management/yc_batch_create_order/";
            this.loading = true;
            this.$axios.post(this._url2 + url, this.formData1).then((res) => {
                if (res.data.code === 200) {
                    alert(res.data.msg);
                    window.location.reload();
                } else {
                    alert(res.data.msg);
                    window.location.reload();
                }
            });
        },
        //易仓txt发货
        submitUpload2() {
            if (this.type === "template" || !this.submitFlag2) return;
            console.log(4);
            let url = "server/order_management/txt_order/";
            this.loading = true;
            this.$axios.post(this._url2 + url, this.formData2).then((res) => {
                if (res.data.code === 200) {
                    alert(res.data.msg);
                    window.location.reload();
                } else {
                    alert(res.data.msg);
                    window.location.reload();
                }
            });
        },
        // 重新发货
        reShip(row) {
            this.dialogVisible1 = true;
            console.log(row);
            let url = "server/order_management/err_order_detail/";
            let that = this;
            this.$axios
                .get(this._url2 + url, { params: { order_id: row } })
                .then((res) => {
                    console.log(res.data);
                    if (res.data.code === 200) {
                        that.ship_one_form1 = res.data.data[0] || {};
                    } else {
                        alert(res.data.msg);
                    }
                });
        },
        // 删除订单
        delShip(row) {
            if (confirm("确定删除")) {
                console.log(row);
                let url = "server/order_management/err_order_delete/";
                let that = this;
                let formData = new FormData();
                formData.append("order_id", row);
                this.$axios.post(this._url2 + url, formData).then((res) => {
                    console.log(res.data);
                    if (res.data.code === 200) {
                        alert("删除成功");
                        window.location.reload();
                    } else {
                        alert(res.data.msg);
                    }
                });
            }
        },
        //获取表格数据
        get_data() {
            let url = "server/order_management/err_order_select/";
            let that = this;
            this.loading = true;
            this.$axios.post(this._url2 + url).then((res) => {
                console.log(res.data);
                if (res.data.code === 200) {
                    that.tableData = res.data.data;
                    that.loading = false;
                } else {
                    alert(res.data.msg);
                }
            });
        },
        //打开修复弹框
        fix_data(row) {
            this.dialogVisible2 = true;
            this.repair_order_id = row;
        },
        //修复提交
        onSubmit2(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    this.dialogVisible2 = false;
                    let url = "server/order_management/repair_defeated_order/";
                    let formData = new FormData();
                    formData.append("order_id", this.repair_order_id);
                    formData.append("order_code", this.ship_one_form2.number);
                    this.$axios.post(this._url2 + url, formData).then((res) => {
                        if (res.data.code === 200) {
                            alert(res.data.msg);
                            window.location.reload();
                        } else {
                            alert(res.data.msg);
                        }
                    });
                } else {
                    console.log("error submit!!");
                    return false;
                }
            });
        },
        //跳转地址超长
        go_long_address() {
            this.$router.push({
                path: "/Address_long",
            });
        },
        //假回传单号查看
        false_order() {
            this.$router.push({
                path: "/FalseOrder",
            });
        },
    },
};
</script>
<style scoped>
.ship .el-form .submitbtn {
    width: 100%;
    margin-left: 0 !important;
}
.ship .el-form {
    border: 1px solid #e5e5e5;
    padding: 40px 50px 20px 0px;
    border-radius: 6px;
    margin-bottom: 20px;
}
.ship .easywarehouse.el-form {
    text-align: center;
    border: 1px solid #e5e5e5;
    border-radius: 6px;
    padding-top: 20px;
}
.ship .easywarehouse.el-form > .el-form-item {
    width: 350px;
    margin: 0px auto;
}
.ship .el-form-item__content {
    text-align: center;
    display: flex;
    align-items: center;
    margin-top: 3px;
}
.ship .el-form-item__content > .el-button.el-button--primary {
    margin-left: -200px;
}
.card_com .ship .el-select {
    width: 100%;
}
</style>
<style scoped>
.ship {
    /* width: 50%;
  margin: 20px auto; */
}
.ship > .shipOption {
    font-size: 16px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.ship > .shipOption > div {
    background: rgba(255, 255, 255, 0.9);
    height: 60px;
    line-height: 60px;
    padding: 0 30px;
    margin: 0 30px 20px;
}
.ship > .shipOption input[type="file"] {
    width: 170px;
}
.div_column {
    display: flex;
    justify-content: space-around;
    width: 60%;
    margin: auto;
}
</style>
<style>
.el-textarea .el-textarea__inner {
    resize: none;
}
</style>



