<template>
  <!-- 提交id和电话号码字段 -->
  <div class="mfyc_template">
    <el-form>
      <el-form-item label="电话">
        <el-input v-model="tel" placeholder="请输入电话"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button @click="submit">提交</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  computed: {
    tel() {
      return this.data.tel;
    },
    id() {
      return this.data.id;
    }
  },
  props: ["type", "data"],
  methods: {
    submit() {
      this.formData = new FormData();
      this.formData.append("id", this.id);
      this.formData.append("phone", this.tel);
      console.log(this.formData);
      let url = "server/order_management/order_edit/";
      this.$axios.post(this._url2 + url, this.formData).then(res => {
        alert(res.data.msg);
        this.$emit("close");
      });
    }
  }
};
</script>

<style scoped>
.mfyc_template {
  display: flex;
}
</style>



