<template>
  <div class="shunfeng">
    <el-form>
      <el-form-item label="魔方云仓批量发货">
        <input
          type="file"
          value="文件上传"
          ref="input3"
          @change="changeFile3"
          accept=".txt"
        />
        <el-button
          style="margin-left: 10px;"
          size="small"
          type="primary"
          @click="submitUpload3"
          >提交</el-button
        >
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      formData3: null
    };
  },
  props: ["type"],
  methods: {
    changeFile3() {
      if (this.type === "template") return;
      console.log(1);
      let files = this.$refs.input3.files[0];
      if (files.name) {
        let hz = files.name.split(".")[1];
        if (hz !== "txt") {
          this.$refs.input3.files = null;
          alert("请上传txt文件");
          return;
        }
      }
      this.formData3 = new FormData();
      this.formData3.append("files", files);
    },
    submitUpload3() {
      if (this.type === "template") return;
      console.log(1);
      let url = "server/order_management/germany_upload/";
      this.$axios.post(this._url2 + url, this.formData3).then(res => {
        if (res.data.code === 200) {
          alert("所有订单创建成功");
        } else {
          alert("订单未创建成功");
        }
      });
    }
  }
};
</script>

<style>
.shunfeng .el-form {
  width: 370px;
  padding: 10px 20px;
  border: 1px solid #e5e5e5;
  margin: 0 auto;
  border-radius: 6px;
  text-align: center;
}
.shunfeng .el-form .el-form-item {
  margin-bottom: 0;
}
</style>



