<template>
  <div class="problem_order">
    <div class="order">
      <div class="left_menu">
        <div>
          <el-select
            filterable
            style="width: 100%"
            v-model="carry"
            @change="carrySearch"
          >
            <el-option
              v-for="(item, i) in carries"
              :value="item"
              :label="item"
              :key="i"
            ></el-option>
          </el-select>
        </div>
        <div>
          <a class="historyLink" @click="toHistoryList">历史记录</a>
        </div>
      </div>
      <div class="order_table">
        <el-table
          ref="poTable"
          max-height="830"
          border
          :data="problem_list"
          style="width: 100%"
        >
          <el-table-column
            type="index"
            :index="indexMethod"
            label="序号"
            width="60"
            align="center"
          >
          </el-table-column>
          <el-table-column
            prop="system_id"
            width="160"
            label="系统单号"
            align="center"
          >
          </el-table-column>
          <el-table-column
            prop="Amazon_id"
            width="160"
            label="订单号"
            align="center"
          >
          </el-table-column>
          <el-table-column
            prop="product_code"
            width="120"
            label="产品编码"
            align="center"
          >
          </el-table-column>
          <el-table-column
            prop="warehouse"
            width="100"
            label="海外仓承运商"
            align="center"
          >
          </el-table-column>
          <el-table-column prop="product_name" label="品名" align="center">
          </el-table-column>
          <el-table-column prop="sku" label="sku" align="center">
          </el-table-column>
          <el-table-column
            prop="country"
            width="80"
            label="国家"
            align="center"
          >
          </el-table-column>
          <el-table-column prop="name" label="收件人" align="center">
          </el-table-column>
          <el-table-column prop="question" label="问题原因" align="center">
          </el-table-column>
          <el-table-column width="80" label="操作" align="center">
            <template slot-scope="scope">
              <!-- <el-button
                @click.native.prevent="editOrder(scope.$index, problem_list)"
                type="text"
                size="small"
              >
                修改订单
              </el-button> -->
              <el-button
                @click.native.prevent="dueSubmit(scope.$index, problem_list)"
                type="text"
                size="small"
              >
                处理
              </el-button>
            </template>
          </el-table-column>
        </el-table>
        <el-pagination
          @current-change="pageChange"
          :current-page.sync="currentPage"
          :page-size="50"
          layout="total, prev, pager, next,jumper"
          :total="totalNum"
        >
        </el-pagination>
      </div>
    </div>
    <el-dialog title="修改订单" :visible.sync="dialogVisible" width="30%">
      <component
        :is="comName"
        :type="'template'"
        :data="dataObj"
        @close="show_editDialog = false"
      ></component>
    </el-dialog>

    <el-dialog title="提示" :visible.sync="dialogVisible1" width="30%">
      <span style="padding-left:40px">是否确认此问题订单已处理</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible1 = false">取 消</el-button>
        <el-button type="primary" @click="submitDue">确 定</el-button>
      </span>
    </el-dialog>
    <!-- <div
      class="order_dialog"
      :style="{
        'z-index': show_editDialog ? 1 : -1,
        display: show_editDialog ? 'block' : 'none'
      }"
    >
      <span class="close_btn" @click="show_editDialog = false">x</span>
    </div> -->
  </div>
</template>

<script>
import MfycProblem from "@/components/orderManagement/FBM/MfycProblem";
import EasyWarehouseCom from "@/components/orderManagement/FBM/EasyWarehouseCom";
import ShunFengCom from "@/components/orderManagement/FBM/ShunFengCom";
export default {
  data() {
    return {
      dialogVisible1: false,
      index: "",
      curData: "",
      // 分页
      dialogVisible: false,
      totalNum: 0,
      currentPage: 1,
      show_editDialog: false,
      comName: "",
      problem_list: [],
      curChangeOrder: [],
      problemData: {
        number: "",
        receive_country: "",
        province: "",
        city: "",
        address: "",
        receive_name: "",
        tel: "",
        postcode: "",
        custom_code: ""
      },
      problemRules: {
        number: [
          { required: true, message: "请输入订单编号", trigger: "blur" }
        ],
        address: [
          { required: true, message: "请输入详细地址", trigger: "blur" }
        ],
        receive_name: [
          { required: true, message: "请输入收货人姓名", trigger: "blur" }
        ],
        tel: [{ required: true, message: "请输入电话", trigger: "blur" }],
        postcode: [{ required: true, message: "请输入邮编", trigger: "blur" }],
        custom_code: [
          { required: true, message: "自定义编码", trigger: "blur" }
        ]
      },
      carries: [
        // {
        //   label: "易仓",
        //   value: "YC"
        // },
        // {
        //   label: "魔方云仓",
        //   value: "MF"
        // },
        // {
        //   label: "加拿大海外仓",
        //   value: "CA_FBM"
        // }
      ],
      carry: "",
      dataObj: {}
    };
  },
  methods: {
    carrySearch() {
      this.currentPage = 1;
      this.changeCarries();
    },
    submitDue() {
      this.dialogVisible1 = false;
      let id = this.curData[this.index].id;
      console.log(this.curData[this.index]);
      let fd = new FormData();
      fd.append("id", id);
      let url = this._url2 + "server/order_management/order_edit/";
      this.$axios.post(url, fd).then(res => {
        if (res.data.code === 200) {
          this.$message.success("请求成功");
          // 刷新
          this.changeCarries();
        } else {
          this.$message.warning("未请求成功");
        }
      });
    },
    indexMethod(index) {
      return index + 1 + 50 * (this.currentPage - 1);
    },
    pageChange(val) {
      this.currentPage = val;
      // 请求
      this.changeCarries();
    },
    dueSubmit(index, data) {
      // 是否确认此问题订单处理
      this.index = index;
      this.curData = data;
      this.dialogVisible1 = true;
    },
    editOrder(index, data) {
      this.dialogVisible = true;
      // 这里可以拿到订单编号，传给子对象。
      this.show_editDialog = true;
      let rowData = data[index];
      let carries = data[index].fbm_carries;
      this.comName =
        carries === "魔方云仓"
          ? "MfycProblem"
          : carries == "易仓"
          ? "EasyWarehouseCom"
          : "ShunFengCom";
      console.log(rowData);
      this.dataObj = {
        id: rowData.name,
        tel: rowData.order_num
      };
    },
    onSubmit(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          alert("submit!");
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    submitUpload() {
      this.$refs.upload.submit();
    },
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePreview(file) {
      console.log(file);
    },
    getSelect() {
      let url = "server/order_management/get_warehouse/";
      this.$axios.get(this._url2 + url).then(res => {
        // res.data.warehouse_list 仓库列表
        this.carries = res.data.warehouse_list;

        if (this.carries.length > 0) {
          this.carry = this.carries[0];
          this.changeCarries();
        }
      });
    },
    changeCarries() {
      let url =
        this._url2 +
        "server/order_management/get_bad_order/?" +
        "page=" +
        this.currentPage +
        "&state=0";
      console.log(url);
      if (this.carry !== "") {
        url += "&warehouse=" + this.carry;
      }

      this.$axios.get(url).then(res => {
        this.problem_list = res.data.data;
        this.totalNum = res.data.total_num;
        this.$nextTick(() => {
          this.$refs["poTable"].bodyWrapper.scrollTop = 0;
        });
      });
    },
    toHistoryList() {
      this.$router.push("/ProblemHistoryList");
    }
  },
  created() {
    this.getSelect();
  },
  components: {
    EasyWarehouseCom,
    MfycProblem,
    ShunFengCom
  }
};
</script>

<style>
/* .problem_order .order_table .el-pagination__total,
.problem_order .order_table .el-pagination__jump {
  color: #fff;
} */
.problem_order .el-table .cell,
.problem_order .el-table--border td:first-child .cell,
.problem_order .el-table--border th:first-child .cell {
  padding: 0px;
}
.problem_order .order_table .el-pagination {
  background: rgba(255, 255, 255, 0.9);
}
.problem_order .el-dialog__header {
  text-indent: 2em;
}
</style>
<style scoped>
.problem_order .order_dialog {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: #fff;
  z-index: 1;
  padding: 38px;
  border: 1px solid #e5e5e5;
  border-radius: 10px;
}
.close_btn {
  position: absolute;
  right: 20px;
  top: 20px;
  cursor: pointer;
}
.order {
  display: flex;
  flex-direction: row;
}
.left_menu {
  width: 200px;
}
.order_table {
  flex: 1;
  padding: 0 1rem;
}
.left_menu > div {
  text-align: center;
}
.historyLink {
  display: inline-block;
  padding: 10px 20px;
  background: dodgerblue;
  margin-top: 80px;
  border-radius: 10px;
  cursor: pointer;
  color: #fff;
  font-size: 16px;
  width: 100%;
}
.order_dialog {
  width: 920px;
  height: 400px;
  overflow-x: auto;
}
</style>



